/* Base styles for the back button */
.back-button {
    margin: 0;
    display: inline-flex;
    align-items: center;
    gap: 0.5rem;
    background: none;
    border: none;
    cursor: pointer;
    padding: 0.3rem 0.5rem;
    background-color: red;
    border-radius: 4px;
    transition: all 0.2s ease;
    position: fixed;
    top: 65px;
    left: 5px;
    z-index: 1000;
}

.back-button:hover {
    background-color: #23ff06;
    color: black;
}

.dark .back-button:hover {
    background-color: #333;
}

@media (max-width: 480px) {
  .back-button {
   display: none;
}

}