/* Profile.css */
.profile-container {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
  background: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.profile-card {
  background: white;
  border-radius: 16px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  width: 100%;
  max-width: 500px;
  overflow: hidden;
}

.profile-header {
  background: linear-gradient(135deg, #6366f1 0%, #4f46e5 100%);
  color: white;
  padding: 30px 20px;
  text-align: center;
}

.profile-avatar {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.2);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 32px;
  font-weight: bold;
  margin: 0 auto 15px;
}

.profile-header h2 {
  margin: 0 0 8px;
  font-size: 24px;
}

.profile-header p {
  margin: 0;
  opacity: 0.9;
}

.profile-details {
  padding: 25px;
}

.profile-details h3 {
  margin: 0 0 20px;
  padding-bottom: 10px;
  border-bottom: 1px solid #eaeaea;
  color: #4f46e5;
  font-size: 18px;
}

.detail-row {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 15px;
  align-items: center;
}

.detail-label {
  font-weight: 600;
  color: #374151;
  width: 120px;
  flex-shrink: 0;
}

.detail-value {
  color: #6b7280;
  flex: 1;
}

.password-container {
  display: flex;
  align-items: center;
  gap: 10px;
  background: #f9fafb;
  padding: 8px 12px;
  border-radius: 6px;
  border: 1px solid #e5e7eb;
}

.show-password-btn {
  background: transparent;
  border: none;
  border-radius: 4px;
  padding: 6px;
  cursor: pointer;
  color: #6b7280;
  transition: all 0.2s;
  display: flex;
  align-items: center;
  justify-content: center;
}

.show-password-btn:hover {
  background: #e5e7eb;
  color: #374151;
}

.profile-actions {
  padding: 20px;
  border-top: 1px solid #eaeaea;
  display: flex;
  gap: 15px;
  justify-content: center;
}

.admin-btn, .logout-btn {
  padding: 12px 20px;
  border-radius: 8px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  text-decoration: none;
  display: inline-block;
  text-align: center;
  font-size: 14px;
}

.admin-btn {
  background: #10b981;
  color: white;
  border: none;
}

.admin-btn:hover {
  background: #059669;
  transform: translateY(-2px);
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.logout-btn {
  background: #ef4444;
  color: white;
  border: none;
}

.logout-btn:hover {
  background: #dc2626;
  transform: translateY(-2px);
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

/* Responsive Design */
@media (max-width: 600px) {
  .profile-container {
    padding: 15px;
  }
  
  .profile-card {
    border-radius: 12px;
  }
  
  .profile-header {
    padding: 20px 15px;
  }
  
  .profile-avatar {
    width: 70px;
    height: 70px;
    font-size: 28px;
  }
  
  .profile-details {
    padding: 20px 15px;
  }
  
  .detail-row {
    flex-direction: column;
    align-items: flex-start;
    gap: 5px;
  }
  
  .detail-label {
    width: 100%;
  }
  
  .password-container {
    width: 100%;
    justify-content: space-between;
  }
  
  .profile-actions {
    flex-direction: column;
    gap: 10px;
  }
  
  .admin-btn, .logout-btn {
    width: 100%;
  }
}

@media (max-width: 400px) {
  .profile-container{
    padding-bottom: 80px;
  }

  .profile-header h2 {
    font-size: 20px;
  }
  
  .profile-details h3 {
    font-size: 16px;
  }
  
  .detail-label, .detail-value {
    font-size: 14px;
  }
}

/* Animation for smoother transitions */
.profile-card {
  animation: fadeIn 0.5s ease-out;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Enhanced focus states for accessibility */
button:focus,
.admin-btn:focus {
  outline: 2px solid #4f46e5;
  outline-offset: 2px;
}

/* Password strength indicator */
.password-strength {
  height: 4px;
  border-radius: 2px;
  margin-top: 6px;
  background: #e5e7eb;
  overflow: hidden;
}

.password-strength-fill {
  height: 100%;
  transition: width 0.3s ease;
}

.password-strength.weak .password-strength-fill {
  background: #ef4444;
  width: 33%;
}

.password-strength.medium .password-strength-fill {
  background: #f59e0b;
  width: 66%;
}

.password-strength.strong .password-strength-fill {
  background: #10b981;
  width: 100%;
}

/* Dark mode support */
@media (prefers-color-scheme: dark) {
  .profile-card {
    background: #1f2937;
    color: #f9fafb;
  }
  
  .profile-details h3 {
    border-bottom-color: #374151;
  }
  
  .detail-label {
    color: #e5e7eb;
  }
  
  .detail-value {
    color: #d1d5db;
  }
  
  .password-container {
    background: #374151;
    border-color: #4b5563;
  }
  
  .show-password-btn {
    color: #9ca3af;
  }
  
  .show-password-btn:hover {
    background: #4b5563;
    color: #e5e7eb;
  }
  
  .profile-actions {
    border-top-color: #374151;
  }
}