/* NotFound.css */
.not-found {
  text-align: center;
  padding: 2rem;
  min-height: 80vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.not-found h1 {
  font-size: 3rem;
  color: #ff4444;
}

.not-found p {
  font-size: 1.2rem;
  margin: 1rem 0;
}

.not-found a {
  color: #0066cc;
  text-decoration: none;
  font-weight: bold;
}

.not-found a:hover {
  text-decoration: underline;
}