/* AdminForm.css */

/* Base Styles */
.admin-form-container {
  margin: 30px auto;
  padding: 25px;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.admin-form-container h2 {
  color: #2c3e50;
  margin-bottom: 25px;
  text-align: center;
  font-size: 28px;
}

/* Form Styles */
.admin-form {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.form-group label {
  font-weight: 600;
  color: #34495e;
  font-size: 15px;
}

.form-group input[type="text"],
.form-group input[type="number"],
.form-group select,
.form-group textarea {
  padding: 12px 15px;
  border: 1px solid #ddd;
  border-radius: 6px;
  font-size: 15px;
  transition: border-color 0.3s;
}

.form-group input[type="text"]:focus,
.form-group input[type="number"]:focus,
.form-group select:focus,
.form-group textarea:focus {
  border-color: #4a6fa5;
  outline: none;
  box-shadow: 0 0 0 2px rgba(74, 111, 165, 0.2);
}

.form-group textarea {
  resize: vertical;
  min-height: 80px;
}

/* File Input Styling */
.form-group input[type="file"] {
  padding: 10px;
  border: 1px dashed #ccc;
  border-radius: 6px;
  background-color: #f9f9f9;
}

.form-group input[type="file"]::file-selector-button {
  padding: 8px 12px;
  background-color: #4a6fa5;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-right: 10px;
  transition: background-color 0.3s;
}

.form-group input[type="file"]::file-selector-button:hover {
  background-color: #3a5a80;
}

/* Add to your existing AdminForm.css */

/* Form Row Layout */
.form-row {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 15px;
  margin-bottom: 20px;
}

/* Responsive adjustments for the row */
@media (max-width: 1024px) {
  .form-row {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 600px) {
  .form-row {
    grid-template-columns: 1fr;
  }
}

/* Adjust form group spacing in the row */
.form-row .form-group {
  margin-bottom: 0;
}

/* Options Grid */
.options-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 15px;
  margin: 10px 0;
}

.options-grid .form-group {
  background-color: #f8f9fa;
  padding: 15px;
  border-radius: 6px;
  border-left: 4px solid #4a6fa5;
}

/* Toggle Switch for Question Type */
.question-type-toggle {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  background-color: #f8f9fa;
  padding: 10px;
  border-radius: 6px;
}

.toggle-label {
  margin-right: 15px;
  font-weight: 600;
  color: #34495e;
}

.toggle-switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 30px;
}

.toggle-switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.toggle-slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  transition: .4s;
  border-radius: 34px;
}

.toggle-slider:before {
  position: absolute;
  content: "";
  height: 22px;
  width: 22px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  transition: .4s;
  border-radius: 50%;
}

input:checked + .toggle-slider {
  background-color: #4a6fa5;
}

input:checked + .toggle-slider:before {
  transform: translateX(30px);
}

.toggle-text {
  margin-left: 10px;
  font-size: 14px;
}

/* Form Actions */
.form-actions {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
  gap: 15px;
}

.submit-btn, .view-btn {
  padding: 12px 25px;
  border: none;
  border-radius: 6px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s;
  flex: 1;
  text-align: center;
}

.submit-btn {
  background-color: #4a6fa5;
  color: white;
}

.submit-btn:hover {
  background-color: #3a5a80;
}

.view-btn {
  background-color: #f8f9fa;
  color: #4a6fa5;
  border: 1px solid #4a6fa5;
}

.view-btn:hover {
  background-color: #e9ecef;
}

/* Conditional Fields */
.conditional-fields {
  display: flex;
  flex-direction: column;
  gap: 20px;
  margin-top: 15px;
}

/* Responsive Styles */
@media (max-width: 768px) {
  .admin-form-container {
    padding: 20px;
    margin: 20px;
  }

  .options-grid {
    grid-template-columns: 1fr;
  }

  .form-actions {
    flex-direction: column;
  }
}

@media (max-width: 480px) {
  .admin-form-container {
    padding: 15px;
    margin: 10px;
  }

  .admin-form-container h2 {
    font-size: 24px;
  }

  .form-group input[type="text"],
  .form-group input[type="number"],
  .form-group select,
  .form-group textarea {
    padding: 10px 12px;
  }
}