.basic-image-display {
  max-width: 1200px;
  margin: 0 auto;
}

.basic-image-display h2 {
  text-align: center;
  color: #333;
  margin-bottom: 30px;
}

.basic-images-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  margin-top: 50px;
  margin-bottom: 30px;
}

.basic-image-wrapper {
  text-align: center;
  background: white;
  border-radius: 10px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.basic-image {
  width: 100%;
  
  height: auto;
 
  object-fit: contain;
  border-radius: 5px;
}

.basic-image-caption {
  margin-top: 10px;
  font-weight: bold;
  color: #666;
}


/* Loading Overlay Styles */
.loading-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.9);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.loading-spinner {
  width: 40px;
  height: 40px;
  border: 4px solid #f3f3f3;
  border-top: 4px solid #3498db;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-bottom: 16px;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.loading-overlay p {
  font-size: 16px;
  color: #333;
  margin: 0;
}

/* Images Container */
.images-container.loading {
  opacity: 0.3;
}

.images-container.loaded {
  opacity: 1;
  transition: opacity 0.3s ease-in;
}

.minimal-image {
  /* Your existing image styles */
  display: block;
}


/* Loading Overlay Styles */
.loading-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.95);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  backdrop-filter: blur(5px);
}

.loading-spinner {
  width: 50px;
  height: 50px;
  border: 5px solid #f3f3f3;
  border-top: 5px solid #3498db;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-bottom: 20px;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.loading-overlay p {
  font-size: 18px;
  color: #333;
  font-weight: bold;
}

/* Optional: Add some styling for when images are loading */
.basic-images-container.loading {
  opacity: 0.3;
  filter: blur(2px);
  transition: opacity 0.3s ease, filter 0.3s ease;
}

.basic-images-container.loaded {
  opacity: 1;
  filter: blur(0);
}

