

.app-container {
  max-width: 800px;
  margin: 0 auto;
}

.notification-app {
  background: white;
  border-radius: 12px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.15);
  overflow: hidden;
}

header {
  color: white;
  padding: 25px;
  text-align: center;
}

header h1 {
  font-size: 2.2rem;
  margin-bottom: 8px;
}

header p {
  opacity: 0.9;
}

.permission-section, .actions-section, .notifications-history {
  padding: 25px;
  border-bottom: 1px solid #eee;
}

.permission-section h2, .actions-section h2, .notifications-history h2 {
  margin-bottom: 16px;
  color: #2c3e50;
  font-size: 1.5rem;
}

.permission-status {
  padding: 12px;
  border-radius: 8px;
  margin-bottom: 16px;
  font-size: 1.1rem;
  background: #f8f9fa;
}

.permission-status.granted {
  background: #d4edda;
  color: #155724;
}

.permission-status.denied {
  background: #f8d7da;
  color: #721c24;
}

.permission-status.default {
  background: #e2e3e5;
  color: #383d41;
}

.permission-status.unsupported {
  background: #fff3cd;
  color: #856404;
}

.permission-help {
  background: #f8f9fa;
  padding: 16px;
  border-radius: 8px;
  margin-top: 16px;
}

.permission-help ol {
  margin-left: 20px;
  margin-top: 10px;
}

.warning {
  color: #856404;
  background: #fff3cd;
  padding: 12px;
  border-radius: 6px;
}

.btn {
  padding: 12px 20px;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-size: 1rem;
  font-weight: 500;
  transition: all 0.2s ease;
}

.btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.btn-primary {
  background: #007bff;
  color: white;
}

.btn-primary:hover:not(:disabled) {
  background: #0069d9;
}

.btn-success {
  background: #28a745;
  color: white;
}

.btn-success:hover:not(:disabled) {
  background: #218838;
}

.btn-warning {
  background: #ffc107;
  color: #212529;
}

.btn-warning:hover:not(:disabled) {
  background: #e0a800;
}

.btn-danger {
  background: #dc3545;
  color: white;
}

.btn-danger:hover:not(:disabled) {
  background: #c82333;
}

.btn-clear {
  background: #6c757d;
  color: white;
}

.btn-clear:hover {
  background: #5a6268;
}

.action-buttons {
  display: flex;
  gap: 12px;
  flex-wrap: wrap;
}

.history-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
}

.empty-state {
  text-align: center;
  padding: 30px;
  color: #6c757d;
  font-style: italic;
}

.notifications-list {
  max-height: 400px;
  overflow-y: auto;
}

.notification-item {
  display: flex;
  padding: 16px;
  border: 1px solid #e9ecef;
  border-radius: 8px;
  margin-bottom: 12px;
  background: #f8f9fa;
}

.notification-item:last-child {
  margin-bottom: 0;
}

.notification-icon {
  font-size: 1.8rem;
  margin-right: 16px;
}

.notification-content {
  flex: 1;
}

.notification-content h3 {
  margin-bottom: 6px;
  color: #2c3e50;
}

.notification-content p {
  margin-bottom: 8px;
  color: #495057;
}

.notification-time {
  font-size: 0.85rem;
  color: #6c757d;
}

@media (max-width: 600px) {
  .action-buttons {
    flex-direction: column;
  }
  
  .history-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 12px;
  }
  
  .btn {
    width: 100%;
  }
}