/* Signup.css */
.signup-container {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
  background-color: #f5f5f5;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.signup-form {
  background: white;
  padding: 25px;
  border-radius: 12px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  width: 100%;
  max-width: 400px;
}

.signup-form h2 {
  text-align: center;
  margin-bottom: 25px;
  color: #333;
  font-size: 24px;
  font-weight: 600;
}

.signup-form label {
  display: block;
  margin-bottom: 8px;
  font-weight: 500;
  color: #374151;
  font-size: 14px;
}

.signup-form input,
.signup-form select {
  width: 100%;
  padding: 12px;
  margin-bottom: 15px;
  border: 1px solid #d1d5db;
  border-radius: 8px;
  font-size: 16px;
  box-sizing: border-box;
  background-color: #f9fafb;
  transition: all 0.2s ease;
}

.signup-form input:focus,
.signup-form select:focus {
  outline: none;
  border-color: #6366f1;
  background-color: white;
  box-shadow: 0 0 0 3px rgba(99, 102, 241, 0.1);
}

/* Phone input styling */
.phone-input-container {
  display: flex;
  margin-bottom: 5px;
}

.country-code {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 12px;
  background: #f3f4f6;
  border: 1px solid #d1d5db;
  border-right: none;
  border-radius: 8px 0 0 8px;
  font-weight: 500;
  color: #374151;
  min-width: 50px;
}

.phone-input-container input {
  border-radius: 0 8px 8px 0;
  margin-bottom: 0;
  flex: 1;
}

.phone-hint {
  font-size: 12px;
  color: #6b7280;
  margin-bottom: 15px;
  margin-top: -5px;
}

/* Error message styling */
.error-message {
  color: #ef4444;
  font-size: 14px;
  margin-bottom: 15px;
  text-align: center;
  padding: 8px;
  background-color: #fef2f2;
  border-radius: 6px;
  border: 1px solid #fecaca;
}

.submit-button {
  width: 100%;
  padding: 14px;
  background-color: #6366f1;
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: background-color 0.2s ease;
  margin-bottom: 15px;
}

.submit-button:hover {
  background-color: #4f46e5;
}

.submit-button:active {
  transform: translateY(1px);
}

.login-link {
  display: block;
  text-align: center;
  color: #6366f1;
  text-decoration: none;
  font-size: 14px;
  font-weight: 500;
  transition: color 0.2s ease;
}

.login-link:hover {
  color: #4f46e5;
  text-decoration: underline;
}

/* Responsive design */
@media (max-width: 480px) {
  .signup-container {
    padding: 15px;
  }
  
  .signup-form {
    padding: 20px;
    border-radius: 10px;
  }
  
  .signup-form h2 {
    font-size: 22px;
    margin-bottom: 20px;
  }
  
  .signup-form input,
  .signup-form select {
    padding: 10px;
    font-size: 15px;
    margin-bottom: 12px;
  }
  
  .submit-button {
    padding: 12px;
    font-size: 15px;
  }
}

@media (max-width: 320px) {
  .signup-form {
    padding: 15px;
  }
  
  .signup-form h2 {
    font-size: 20px;
  }
  
  .signup-form input,
  .signup-form select {
    padding: 8px;
    font-size: 14px;
  }
  
  .country-code {
    padding: 0 8px;
    min-width: 45px;
    font-size: 14px;
  }
}