/* Login.css */
.login-container {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px;
  background-color: #f5f5f5;
}

.login-container h2 {
  text-align: center;
  margin-bottom: 25px;
  color: #333;
  font-size: 28px;
  font-weight: 600;
  width: 100%;
  max-width: 400px;
}

.login-form {
  background: white;
  padding: 25px;
  border-radius: 12px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  width: 100%;
  max-width: 400px;
}

.login-form label {
  display: block;
  margin-bottom: 8px;
  font-weight: 500;
  color: #374151;
  font-size: 14px;
}

.login-form input {
  width: 100%;
  padding: 12px;
  margin-bottom: 20px;
  border: 1px solid #d1d5db;
  border-radius: 8px;
  font-size: 16px;
  box-sizing: border-box;
  background-color: #f9fafb;
  transition: all 0.2s ease;
}

.login-form input:focus {
  outline: none;
  border-color: #6366f1;
  background-color: white;
  box-shadow: 0 0 0 3px rgba(99, 102, 241, 0.1);
}

.error-message {
  color: #ef4444;
  font-size: 14px;
  margin-bottom: 15px;
  text-align: center;
  padding: 8px;
  background-color: #fef2f2;
  border-radius: 6px;
  border: 1px solid #fecaca;
}

.submit-button {
  width: 100%;
  padding: 14px;
  background-color: #6366f1;
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease;
  margin-bottom: 15px;
}

.submit-button:hover {
  background-color: #4f46e5;
}

.submit-button:active {
  transform: translateY(1px);
}

.login-form a {
  display: block;
  text-align: center;
  color: #6366f1;
  text-decoration: none;
  font-size: 14px;
  font-weight: 500;
  transition: color 0.2s ease;
  margin-top: 10px;
}

.login-form a:hover {
  color: #4f46e5;
  text-decoration: underline;
}

/* Media Queries for smaller mobile devices */
@media (max-width: 480px) {
  .login-container {
    padding: 5px;
  }
  
  .login-container h2 {
    font-size: 24px;
    margin-bottom: 20px;
  }
  
  .login-form {
    padding: 20px;
    border-radius: 10px;
  }
  
  .login-form input {
    padding: 10px;
    font-size: 15px;
    margin-bottom: 15px;
  }
  
  .submit-button {
    padding: 12px;
    font-size: 15px;
  }
}

@media (max-width: 320px) {
  .login-container h2 {
    font-size: 22px;
  }
  
  .login-form {
    padding: 5px;
  }
  
  .login-form input {
    padding: 8px;
    font-size: 14px;
  }
  
  .submit-button {
    padding: 10px;
    font-size: 14px;
  }
}

/* Landscape mode optimization */
@media (max-height: 500px) and (orientation: landscape) {
  .login-container {
    padding: 5px;
    justify-content: flex-start;
  }
  
  .login-form {
    max-height: 80vh;
    overflow-y: auto;
  }
  
  .login-form input {
    margin-bottom: 12px;
    padding: 8px;
  }
}

/* Reduced motion for accessibility */
@media (prefers-reduced-motion: reduce) {
  .login-form input,
  .submit-button,
  .login-form a {
    transition: none;
  }
  
  .submit-button:active {
    transform: none;
  }
}

/* High contrast mode support */
@media (prefers-contrast: high) {
  .login-form {
    border: 2px solid #000;
  }
  
  .login-form input {
    border: 2px solid #000;
  }
  
  .submit-button {
    border: 2px solid #000;
  }
}