.upgrade-container {
  display: flex;
  justify-content: center;
  align-items: center;

  font-family: Arial, sans-serif;

}

.upgrade-box {
  background-color: #ffffff;
  padding: 40px;
  border-radius: 12px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
  text-align: center;
  
}

.upgrade-title {
  color: #333;
  margin-bottom: 15px;
  font-size: 1.8rem;
}

.upgrade-message {
  color: #666;
  font-size: 1rem;
  line-height: 1.5;
  margin-bottom: 25px;
}

.upgrade-link {
  background-color: #007bff;
  text-decoration: none;
  color: white;
  padding: 12px 24px;
  border: none;
  border-radius: 8px;
  font-size: 1rem;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.upgrade-button:hover {
  background-color: #0056b3;
}