/* Shared Styles for Bookmarked Content */
.bookmarked-container {
  max-width: 1200px;
  margin: 2rem auto;
  margin-top: 0px;
  padding: 0.5rem;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  color: #333;
  padding-bottom: 60px;
}

.header {
 
  text-align: center;

}

/* Filter Controls */
.filter-controls {
  display: flex;
  gap: 0.3rem;
  margin-bottom: 1rem;
  justify-content: center;
}

.filter-btn {
  padding: 0.5rem 0.5rem;
  border: none;
  border-radius: 20px;
  background-color: #eeeeee;
  color: #2c3e50;
  cursor: pointer;
  transition: all 0.3s ease;
  font-weight: 500;
}

.filter-btn:hover {
  background-color: #3498db;
  color: white;
}

.filter-btn.active {
  background-color: #2980b9;
  color: white;
}

/* Pagination Controls */
.pagination-controls {
  display: flex;
  gap: 10px;
  align-items: center;
  margin: 1rem 0;
  padding: 0.4rem;
  background-color: #f8f9fa;
  border-radius: 8px;
}

.pagination-info {
  font-size: 0.9rem;
  color: #7f8c8d;
}

.pagination-buttons {
  display: flex;
  gap: 0.8rem;
}

.pagination-btn {
  padding: 0.5rem 1rem;
  border: 1px solid #bdc3c7;
  border-radius: 5px;
  background-color: rgb(47, 202, 194);
  cursor: pointer;
  transition: all 0.2s ease;
}

.pagination-btn:hover {
  background-color: #3498db;
  color: white;
  border-color: #3498db;
}

.pagination-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
  background-color: #545f63;
}

/* Items List */
.items-list {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.item-card {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5px;
  background-color: white;
  border-radius: 8px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  transition: transform 0.2s ease;
}

.item-card:hover {
  transform: translateY(-3px);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.15);
}

.item-content {
  flex: 1;
  display: flex;
  align-items: center;
  gap: 1rem;
}

.item-index {
  float: left;
  font-size: 15px;
  color: #3498db;
  min-width: 30px;
}

.item-text {
  font-size: 15px;
  flex: 1;
  margin: 0;
  line-height: 1.5;
  font-weight: 400;
}

.item-image {
  max-width: 200px;
  max-height: 100px;
  border-radius: 4px;
  object-fit: contain;
  border: 1px solid #eee;
}

.item-meta {
  font-size: 13px;
  color: #7f8c8d;
  margin-top: 0.3rem;
}



.item-actions {
  display: grid;
  grid-auto-flow: column;
  gap: 10px;
}



.view-btn {
  font-size: 11px;
  text-decoration: none;
  background-color: #3498db;
  color: white;
}

.view-btn:hover {
  background-color: #2980b9;
}

.remove-btn {
   font-size: 11px;
  text-decoration: none;
  background-color: #e74c3c;
  color: white;
}

.remove-btn:hover {
  background-color: #c0392b;
}

/* Empty State */
.empty-state {
  text-align: center;
  padding: 3rem;
  background-color: white;
  border-radius: 8px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.empty-message {
  font-size: 1.1rem;
  margin-bottom: 1.5rem;
  color: #7f8c8d;
}

.browse-link {
  display: inline-block;
  padding: 0.7rem 1.5rem;
  background-color: #2ecc71;
  color: white;
  text-decoration: none;
  border-radius: 5px;
  transition: all 0.2s ease;
}

.browse-link:hover {
  background-color: #27ae60;
}

.error-message {
  color: #e74c3c;
  padding: 1rem;
  background-color: #fdecea;
  border-radius: 5px;
  text-align: center;
  margin: 2rem auto;
  max-width: 600px;
}


/* Clear All Button */
.clear-all-btn {
  background-color: #e74c3c;
  color: white;
  border: none;
  padding: 8px 16px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
  transition: background-color 0.3s;
}

.clear-all-btn:hover {
  background-color: #c0392b;
}

/* Confirmation Modal */
.confirmation-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal-content {
  background-color: white;
  padding: 20px;
  border-radius: 8px;
  max-width: 400px;
  width: 100%;
  text-align: center;
}

.modal-content h3 {
  margin-top: 0;
  color: #333;
}

.modal-content p {
  margin-bottom: 20px;
  color: #666;
}

.modal-actions {
  display: flex;
  justify-content: center;
  gap: 10px;
}

.cancel-btn,
.confirm-btn {
  padding: 8px 16px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
}

.cancel-btn {
  background-color: #ecf0f1;
  color: #333;
}

.cancel-btn:hover {
  background-color: #bdc3c7;
}

.confirm-btn {
  background-color: #e74c3c;
  color: white;
}

.confirm-btn:hover {
  background-color: #c0392b;
}

/* Responsive Design */
@media (max-width: 768px) {
  .item-card {
    flex-direction: column;
    align-items: flex-start;
    gap: 1rem;
  }

  .item-actions {
    width: 100%;
    justify-content: flex-end;
  }

  .filter-controls {
    justify-content: flex-start;
  }
}

@media (max-width: 480px) {
  .bookmarked-header {
    font-size: 1.5rem;
  }

  .pagination-controls {
    flex-direction: column;
    gap: 1rem;
    align-items: stretch;
  }

  .item-image {
    max-width: 100%;
  }

  .item-text {
  font-size: 9px;
  flex: 1;
  margin: 0;
  line-height: 1.5;
  font-weight: 400;
}

.item-meta {
  font-size: 7px;
  color: #7f8c8d;
  margin-top: 0.3rem;
}

  .item-actions {
    flex-direction: column;
    gap: 0.5rem;
    width: 100%;
  }

  .action-btn {
    width: 100%;
  }

  .view-btn {
    padding: 5px;
  font-size: 8px;
  text-decoration: none;
  background-color: #3498db;
  color: white;
}


.remove-btn {
    padding: 5px;
   font-size: 8px;
  text-decoration: none;
  background-color: #e74c3c;
  color: white;
}


}