/* AdminQuizQuestion.css - Responsive Version */

/* Base Styles */
.admin-quiz-container {
 
  margin: 0 auto;
 
}

.loading-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 200px;
}

.spinner {
  border: 4px solid rgba(0, 0, 0, 0.1);
  border-radius: 50%;
  border-top: 4px solid #3498db;
  width: 40px;
  height: 40px;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.empty-state {
  text-align: center;
  padding: 30px;
  background: #f8f9fa;
  border-radius: 8px;
  margin: 20px 0;
}

.add-quiz-btn {
  background-color: #4CAF50;
  color: white;
  padding: 10px 15px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
  margin-bottom: 20px;
  width: 100%;
}

.add-quiz-btn:hover {
  background-color: #45a049;
}

/* Filter Section - Responsive */
.quiz-filters {
  display: flex;
  background: #ffffff;
  padding: 15px;
  border-radius: 8px;
  margin-bottom: 10px;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 5px;
}

.filter-group {
 padding: 5px;
  margin-bottom: 0;
  background-color: hsl(0, 0%, 97%);
}

.filter-group h3 {
  margin-top: 0;
  margin-bottom: 10px;
  font-size: 16px;
  color: #333;
}

.filter-buttons {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}

.filter-btn {
  padding: 6px 12px;
  background: #e0e0e0;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
  transition: all 0.2s;
  flex-grow: 1;
  text-align: center;
  min-width: 60px;
}

.filter-btn:hover {
  background: #d0d0d0;
}

.filter-btn.active {
  background: #4CAF50;
  color: white;
}

.reset-filters-btn {
  background: #6c757d;
  color: white;
  border: none;
  padding: 8px 15px;
  border-radius: 4px;
  cursor: pointer;
  margin-top: 10px;
  font-size: 14px;
}

.reset-filters-btn:hover {
  background: #5a6268;
}

/* Table Styles - Responsive */
.quiz-table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 20px;
}

.quiz-table th {
  background-color: #4CAF50;
  color: white;
  text-align: left;
  padding: 12px;
}

.quiz-table td {
  padding: 12px;
  border-bottom: 1px solid #ddd;
  vertical-align: top;
}

.quiz-table tr:nth-child(even) {
  background-color: #f2f2f2;
}

.quiz-table tr:hover {
  background-color: #e9e9e9;
}

/* Cell Specific Styles */
.image-cell img.question-image {
  max-width: 100%;
  height: auto;
  cursor: pointer;
  border-radius: 4px;
}

.options-container {
  margin-bottom: 10px;
}

.options-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 8px;
  margin-top: 8px;
}

.option-item {
  padding: 6px;
  border-radius: 4px;
  background: #e9ecef;
}

.option-item.correct {
  background: #d4edda;
  color: #155724;
}

.meta-cell div {
  margin-bottom: 6px;
}

.action-buttons {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.solution-btn,
.video-btn,
.update-btn,
.delete-btn {
  padding: 8px 12px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
  text-align: center;
}

.solution-btn {
  background: #17a2b8;
  color: white;
}

.video-btn {
  background: #6c757d;
  color: white;
}

.update-btn {
  background: #ffc107;
  color: #212529;
}

.delete-btn {
  background: #dc3545;
  color: white;
}

/* Image Modal */
.image-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal-content {
  position: relative;
  max-width: 90%;
  max-height: 90%;
}

.modal-content img {
  max-width: 100%;
  max-height: 80vh;
  border-radius: 4px;
}

.close-modal {
  position: absolute;
  top: -40px;
  right: 0;
  color: white;
  font-size: 30px;
  cursor: pointer;
}

/* Responsive Adjustments */
@media (max-width: 992px) {
  .quiz-filters {
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  }

  .options-grid {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 768px) {
  .quiz-table thead {
    display: none;
  }

  .quiz-table tr {
    display: block;
    margin-bottom: 15px;
    border: 1px solid #ddd;
    border-radius: 4px;
  }

  .quiz-table td {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 8px 12px;
    border-bottom: 1px solid #eee;
  }

  .quiz-table td:before {
    content: attr(data-label);
    font-weight: bold;
    margin-right: 15px;
    flex: 1;
  }

  .quiz-table td:last-child {
    border-bottom: none;
  }

  .action-buttons {
    flex-direction: row;
    flex-wrap: wrap;
  }

  .action-buttons button {
    flex: 1 0 45%;
  }

  .filter-buttons {
    flex-direction: column;
  }
}

@media (max-width: 480px) {
  .quiz-filters {
    grid-template-columns: 1fr;
  }

  .add-quiz-btn {
    font-size: 14px;
    padding: 8px 12px;
  }

  .action-buttons button {
    flex: 1 0 100%;
  }
}